<template>
  <div>
    <CollapsibleA title="標題 1" content="內容 1" :active-section="1" :selected-option="1" @click="selected = 1" :is-selected="selected === 1"/>
    <CollapsibleA title="標題 2" content="內容 2" :active-section="2" :selected-option="2" @click="selected = 2"/>
    <CollapsibleA title="標題 3" content="內容 3" :active-section="3" :selected-option="3" @click="selected = 3"/>
  </div>
</template>

<script>
import CollapsibleA from "@/components/CollapsibleA.vue";

export default {
  components: {
    CollapsibleA
  },  
  watch: {
    selected(newValue, oldValue) {
      console.log('selectedOption changed from', oldValue, 'to', newValue);
      // 在此處更新 Child 組件的狀態
    }
  },
  data() {
    return {
      selected: 1
    };
  }
};
</script>
