<template>

    <div class="slidecontenct">
  <div class="slide" @mousedown="startDragging" @mousemove="onDragging" @mouseup="stopDragging">
    <transition name="fade">
      <div :style="{ backgroundImage: `url(${images[currentIndex]})` }" class="slide-image" >
        <div class="slide-content">
          <h3 class="slide-title">{{ titles[currentIndex] }}</h3>
          <p class="slide-description">{{ descriptions[currentIndex] }}</p>
        </div>
      </div>
    </transition>
  </div>
</div>

</template>

<script>
export default {
  data() {
    return {
      images: ['https://picsum.photos/300/300?random=10', 'https://picsum.photos/300/300?random=9', 'https://picsum.photos/300/300?random=8', 'https://picsum.photos/300/300?random=9', 'https://picsum.photos/300/300?random=8'],
      titles: ['Hans', 'Benny', 'john','Laozhou','Yushan'],
      descriptions: ['Description 1', 'Description 2', 'Description 3', 'Description 4', 'Description 5'],
      currentIndex: 0,
      startX: 0,
      isDragging: false,
      intervalId: null
    };
  },
  mounted() {
    // 自動撥放
    this.intervalId = setInterval(() => {
      this.next();
    }, 5000);
  },
  beforeUnmount() {
    // 結束自動撥放
    clearInterval(this.intervalId);
  },
  methods: {
    startDragging(e) {
      this.isDragging = true;
      this.startX = e.clientX;
    },
    onDragging(e) {
      if (this.isDragging) {
        const delta = e.clientX - this.startX;
        if (delta > 20) {
          this.prev();
          this.isDragging = false;
        } else if (delta < -20) {
          this.next();
          this.isDragging = false;
        }
      }
    },
    stopDragging() {
      this.isDragging = false;
    },
    next() {
      this.currentIndex = (this.currentIndex + 1) % this.images.length;
    },
    prev() {
      this.currentIndex = (this.currentIndex + this.images.length - 1) % this.images.length;
    }
  }
};
</script>

