<template>
    <div class="flicker-text-container">
      <div :style="{ backgroundColor: bgColor }" class="flicker-text-mask"  ></div>
      <div class="flicker-text" ref="text">{{ text }}</div>
      <h3 >{{ textMin }}</h3>
    </div>
</template>
  
<script>
  export default {
    props: {
      text: {
        type: String,
        default: ''
      },      
      textMin: {
        type: String,
        default: ''
      },
      bgColor:
      {
        type: String,
        default: "'red'"
      }
    },
    data() {
      return {
        isShow: false,
      
      };
    },
    mounted() {
      window.addEventListener('scroll', this.handleScroll);
    },
    beforeUnmount() {
      window.removeEventListener('scroll', this.handleScroll);
    },
    methods: {
      handleScroll() {
        const containerRect = this.$el.getBoundingClientRect();
        if (
          !this.isShow &&
          containerRect.top >= 0 &&
          containerRect.bottom <= window.innerHeight
        ) {
          this.isShow = true;
          this.$refs.text.classList.add('flicker-effect');
        }
      }
    }, 
    created() {
        if(this.isShow)
            this.isShow = false;
  },
  };
</script>
<style>
h3{
    color: #fff;
}
.flicker-text-container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.flicker-text-mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.5;
  z-index: 2;
}

.flicker-text {
  position: relative;
  z-index: 3;
  text-align: center;
  font-size: 4rem;
  font-weight: bold;
  color: #fff;
  margin-top: 5vh;
  opacity: 0;
  transition: opacity 1s ease;
}

.flicker-effect {
    animation: flicker 3s forwards; /* 使用閃螢光動畫，並保持在最後的狀態 */ 

  opacity: 1 !important;
}

@keyframes flicker {
    0% {
    opacity: 1;
    text-shadow: 0 0 30px #f12711, 0 0 30px #f12711; /* 設定文字陰影效果 */
  }
  5% {
    opacity: 0.8;
    text-shadow: none; /* 取消文字陰影效果 */
  }
  10% {
    opacity: 1;
    text-shadow: 0 0 20px #f12711, 0 0 20px #f12711; /* 設定文字陰影效果 */
  }
  15% {
    opacity: 0.8;
    text-shadow: none; /* 取消文字陰影效果 */
  }
  20% {
    opacity: 1;
    text-shadow: 0 0 10px #f12711, 0 0 10px #f12711; /* 設定文字陰影效果 */
  }
  25% {
    opacity: 0.8;
    text-shadow: none; /* 取消文字陰影效果 */
  }
  30% {
    opacity: 1;
    text-shadow: 0 0 20px #f5af19, 0 0 20px #f5af19; /* 設定文字陰影效果 */
  }
  35% {
    opacity: 0.8;
    text-shadow: none; /* 取消文字陰影效果 */
  }
  40% {
    opacity: 1;
    text-shadow: 0 0 10px #f5af19, 0 0 10px #f5af19; /* 設定文字陰影效果 */
  }
  45% {
    opacity: 0.8;
    text-shadow: none; /* 取消文字陰影效果 */
  }
  50% {
    opacity: 1;
    text-shadow: 0 0 5px #f5af19, 0 0 5px #f5af19; 
  }
  100% {
    opacity: 1;
    text-shadow: 0 0 5px #f5af19, 0 0 5px #f5af19; 
  }

}

</style>